.menu {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
}

.active {
    color: orangered;
    font-weight: bold;
}

.activ {
    text-decoration: none;
    font-size: large;
    font-family: cursive;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-content: center;
}

.bt {
    color: black;
    border: 1px solid black;
    border-radius: 20px;
    text-align: center;
    box-shadow: 0px 1px 1px 0px gray;
    background-color: rgba(255, 166, 0, 1);
    width: 65px;
}

.activ :hover {
    color: orange;
}

@media (max-width: 1160px) {
    .menu {
        margin-bottom: 10px;
    }

    .activ {
        margin-top: 5px;
    }
}